.error {
  position: absolute;
  font-size: 12px;
  color: var(--amount-expenses-text-color);
  font-weight: 500;
  top: 0%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 50;
}

@media screen and (min-width: 768px) {
  .error {
    font-size: 14px;
  }
}
